import { useContext } from "react";
import { NotesContext } from "../../context";
import Note from "./components/Note";

export default function DisplayNotes() {
  const { notes } = useContext(NotesContext);
  return (
    <div
      style={{
        marginTop: "16px",
        display: "flex",
        flexWrap: "wrap",
        gap: "12px"
      }}
    >
      {notes.map((elem) => {
        return <Note elem={elem} key={elem.id} />;
      })}
    </div>
  );
}
